    font-weight: 500;
}
.nav-link{
    color: #FFF;
    text-decoration: none !important;
    padding: 1.2em 0;
    display: block;
}
.nav-link:hover{
    text-decoration: none;
    color: #DDD;
    background-color: #222;
}
.nav-link:visited{
    color: #FFF;
}
.nav-slide-enter, .nav-slide-enter-active, .nav-active-enter-done{
    -webkit-animation-name: slideNavIn;
            animation-name: slideNavIn;
    -webkit-animation-duration: 750ms;
            animation-duration: 750ms;
}
.nav-slide-exit, .nav-slide-exit-active, .nav-slide-exit-done{
    -webkit-animation-name: slideNavOut;
            animation-name: slideNavOut;
    -webkit-animation-duration: 750ms;
            animation-duration: 750ms;
}
@-webkit-keyframes slideNavIn{
    from{
        left: -20em;
    }
    to{
        left: 0;
    }
}
@keyframes slideNavIn{
    from{
        left: -20em;
    }
    to{
        left: 0;
    }
}
@-webkit-keyframes slideNavOut{
    from{
        left: 0;
    }
    to{
        left: -20em;
    }
}
@keyframes slideNavOut{
    from{
        left: 0;
    }
    to{
        left: -20em;
    }
}
